.featuredProducts {
    margin: 50px 100px;
  
    .top {
      display: flex;
      align-items:flex-start;
      margin-bottom: 20px;
  
      h1 {
        flex: 2;
        text-transform: capitalize;
      }
    }
  
    .bottom{
      display: grid;
      grid-template-columns: 2fr 2fr 2fr 2fr;
      grid-auto-rows: minmax(200px,auto);
      justify-content: center;
      align-content: center;
      gap: 10px;
    }
  }