/* Footer.css */

.footer {
    margin: 50px 100px 0px 100px;
    border-top: 1px solid #292727;
  }
  
  .top {
    display: flex;
    justify-content: space-between; /* Space between the two columns */
    margin-left:50px;
    margin-right: 50px;
  }
  
  .column {
    width: 40%; /* Minimum width for each column */
    height: auto;
    flex-direction: column;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  
  .icon {
    margin-right: 8px; /* Space between the icon and the text */
  }
  .ins-link{
    text-decoration: none;
    color:#ff4dc4;
  }
  .ins-link:hover{
    text-decoration: underline;
  }
  .email-link {
    text-decoration: none;
    color:#ff4dc4;
  }
  
  .email-link:hover {
    text-decoration: underline;
  }

  p{
    font-family: cursive;
    font-size: 20px;
    color:#ff4dc4;
  }
  h1{
    color:#ff4dc4;
    font-family: cursive;

  }

  span{
    color:#ff4dc4;
    font-family: cursive;
    font-size: 20px;
  }
  
