.bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 50px;
    margin-right:50px;
    margin-top:20px;
    
    .left {
      display: flex;
      align-items: center;

      .copyright{
        font-size: 12px;
        color: gray;
      }
    }
    .right {

      img{
        margin-right:0px;
        padding-right:0px;
        height: auto;
        width: 700px;
      }
    }
}